<template>
  <div class="bianji_wrap">
   <headers :title="title"></headers>
   <p class="success" @click="changeSwitch">完成</p>
    <div class="lanmu">

        <van-cell-group>
          <van-switch-cell v-for="obj in sectionByUser"
              :key="obj.pType"
              v-model="obj.isOpen"
              :title="obj.name" />
        </van-cell-group>

    </div>
  </div>
</template>
<script>
  import "lib-flexible"
  import axios from '../../config/http'
  import headers from '../components/editHeader.vue'
  import {Toast} from 'vant'
  export default {
    data() {
      return {
        title: "编辑栏目",
        sectionByUser:[]
      };
    },
    components: {
      headers
    },
    created () {
      let _this = this
      this.$http.all([this.getSectionByUser()])
        .then(axios.spread(function(getSectionByUser){
        const { section } = getSectionByUser.data
        console.log('sec ',section )
        for(const cont of section){

          if(cont.isOpen=='on'){
              cont.isOpen=true
          }else if( cont.isOpen =='off'){
              cont.isOpen=false
          }

        }
         _this.sectionByUser = section
         for(const i of section){
            console.log('_this  : => ', i)
         }
        console.log('_this.sectionByUser  : => ', _this.sectionByUser)
      }))
    },
    methods: {
      getSectionByUser() {
        return this.$get('guanjia_heard/homepage/privilege/getSectionByUser')
      },
      changeSwitch(checked){
        let section = this.sectionByUser;
        let sectionShowObj = new Array();
        for(const obj of section){
          if(obj.isOpen){
            sectionShowObj.push(obj.pType)
          }

        }
        let pDetailIds = sectionShowObj.join();
        let pType = 2;
        let data = { pType, pDetailIds };
        this.$post('guanjia_heard/homepage/privilege/editPrivilege', data).then((res)=>{
            if(res.code==200){
              Toast('设置成功')
            }else{
              Toast(res.res)
            }
          })


      }
    }
  };
</script>
<style scoped>
  .bianji_wrap {
    flex: 1;
  }

  .success {
    color: #fff;
    font-size: 36px;
    position: absolute;
    right: 40px;
    top: 34px;
    z-index: 9999;
  }

  .lanmu {
    width: 95%;
    float: right;
    margin-top: 90px;
  }
</style>



